<template>
  <div v-if="userLogin['role_name'] === 'admin'">
    <v-container
      id="users"
      fluid
      tag="section"
      :style="breakpointName === 'xs' || breakpointName === 'sm' ? 'padding-top: 75px' : ''"
    >
      <base-material-card
        style="margin-top: 20px"
        icon="mdi-clipboard-text"
        title="Danh sch c s y t"
        class="px-5 py-3"
      >
          <v-card-text :class="breakpointName !== 'lg' ? 'px-0' : ''">
            <div :class="breakpointName === 'xs' ? 'mb-3' : 'd-flex mb-3'">
              <div class="mr-auto pt-2 mb-3" v-if="breakpointName === 'xs'">
                Tng s: <span style="font-weight: bold; color: green">{{totalItem}}</span> c s y t
              </div>
              <span class="mr-auto pt-2" v-else>
                Tng s: <span style="font-weight: bold; color: green">{{totalItem}}</span> c s y t
              </span>
              <v-btn color="#0072bc" class="mx-0" @click.stop="addMember('add')">
                <v-icon left size="22">
                  mdi-plus
                </v-icon>
                Thm c s y t
              </v-btn>
            </div>
            <v-data-table
              :headers="headers"
              :items="items"
              :page.sync="page"
              :items-per-page="itemsPerPage"
              hide-default-footer
              class="elevation-1"
              @page-count="pageCount = $event"
              no-data-text="Khng c"
              :loading="loadingData"
              loading-text="ang ti... "
            >
            <template v-slot:item.index="{ item, index }">
              <span>{{ index + 1 }}</span>
            </template>
            <template v-slot:item.action="{ item }">
              <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn @click="addMember('update', item)" color="blue" text icon class="" v-bind="attrs" v-on="on">
                    <v-icon size="22">mdi-pencil</v-icon>
                  </v-btn>
                </template>
                <span>Sa</span>
              </v-tooltip>
              <v-tooltip top >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn @click="deleteCoSo(item)" color="red" text icon class="" v-bind="attrs" v-on="on">
                    <v-icon size="22">mdi-delete</v-icon>
                  </v-btn>
                </template>
                <span>Xa</span>
              </v-tooltip>
            </template>
            
            </v-data-table>
            <div class="text-center mt-4">
              <v-pagination
                v-model="page"
                :length="pageCount"
              ></v-pagination>
            </div>
          </v-card-text>
      </base-material-card>
      <v-dialog
        max-width="900"
        v-model="dialogAddMember"
      >
        <v-card>
          <v-toolbar
            dark
            color="#0072bc"
          >
            <v-toolbar-title v-if="typeAction === 'add'">Thm c s y t</v-toolbar-title>
            <v-toolbar-title v-else>Cp nht thng tin</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn
                icon
                dark
                @click="dialogAddMember = false"
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </v-toolbar-items>
          </v-toolbar>
          <v-card-text class="mt-5">
            <v-form
              ref="formAddMember"
              v-model="validFormAdd"
              lazy-validation
            >
                <v-layout wrap>
                    <v-text-field
                        class="flex xs12 md6"
                        v-model="thongTinCoSo.MaCoSo"
                        outlined
                        label="M c s"
                        prepend-inner-icon="mdi-account-check-outline"
                        dense
                        clearable
                    ></v-text-field>
                    <v-text-field
                        class="flex xs12 md6 pl-2"
                        v-model="thongTinCoSo.TenCoSo"
                        outlined
                        label="Tn c s"
                        prepend-inner-icon="mdi-account-check-outline"
                        dense
                        clearable
                    ></v-text-field>
                    <v-text-field
                        class="flex xs12 md12"
                        v-model="thongTinCoSo.DiaChiCoSo"
                        outlined
                        label="a ch c s"
                        prepend-inner-icon="mdi-account-check-outline"
                        dense
                        clearable
                    ></v-text-field>
                    <v-autocomplete
                        class="flex xs12 md4"
                        hide-no-data
                        :items="listTinhThanh"
                        v-model="tinhThanh"
                        item-text="tinhThanhTen"
                        item-value="tinhThanhMa"
                        clearable
                        :rules="required"
                        required
                        outlined
                        label="Tnh/ Thnh ph"
                        dense
                    ></v-autocomplete>
                    <v-autocomplete
                        class="flex xs12 md4 pl-2"
                        hide-no-data
                        :items="listQuanHuyen"
                        v-model="quanHuyen"
                        item-text="quanHuyenTen"
                        item-value="quanHuyenMa"
                        clearable
                        :rules="required"
                        required
                        outlined
                        label="Qun/ Huyn"
                        dense
                    ></v-autocomplete>
                    <v-autocomplete
                        class="flex xs12 md4 pl-2"
                        hide-no-data
                        :items="listXaPhuong"
                        v-model="xaPhuong"
                        item-text="phuongXaTen"
                        item-value="phuongXaMa"
                        clearable
                        :rules="required"
                        required
                        outlined
                        label="Phng/ X"
                        dense
                    ></v-autocomplete>
                    <v-text-field
                        class="flex xs12 md6"
                        v-model="thongTinCoSo.NguoiDaiDien"
                        outlined
                        label="Ngi i din"
                        prepend-inner-icon="mdi-account-check-outline"
                        dense
                        clearable
                    ></v-text-field>
                    <v-text-field
                        class="flex xs12 md6 pl-2"
                        v-model="thongTinCoSo.SoDienThoai"
                        outlined
                        label="S in thoi"
                        prepend-inner-icon="mdi-account-check-outline"
                        dense
                        clearable
                    ></v-text-field>
                </v-layout>
            </v-form>
          </v-card-text>
          <v-card-actions class="justify-end">
            
            <v-btn color="red" class="white--text mr-2" :loading="loading" :disabled="loading" @click="cancelAddMember">
              <v-icon left>
                mdi-close
              </v-icon>
              Thot
            </v-btn>
            <v-btn class="mr-2" color="#0072bc" :loading="loading" :disabled="loading" @click="submitAddMember">
              <v-icon left>
                mdi-content-save
              </v-icon>
              <span v-if="typeAction === 'add'">Thm mi</span>
              <span v-else>Cp nht</span>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
    
  </div>
  
</template>

<script>
  export default {
    name: 'Users',

    data () {
      return {
        loading: false,
        loadingData: false,
        dialogAddMember: false,
        thongTinCoSo: {
          MaCoSo: '',
          TenCoSo: '',
          TinhThanh_Ma: '',
          TinhThanh_Ten: '',
          QuanHuyen_Ma: '',
          QuanHuyen_Ten: '',
          PhuongXa_Ma: '',
          PhuongXa_Ten: '',
          DiaChiCoSo: '',
          NguoiDaiDien: '',
          SoDienThoai: ''
        },
        listTinhThanh: [],
        tinhThanh: '',
        listQuanHuyen: [],
        quanHuyen: '',
        listXaPhuong: [],
        xaPhuong: '',
        totalItem: 0,
        page: 1,
        pageCount: 0,
        itemsPerPage: 5,
        typeAction: '',
        coSoUpdate: '',
        items: [],
        validFormAdd: true,
        required: [
          (value) => {
            if(String(value).trim()){
                return true
              } else {
                return 'Thng tin bt buc'
              } 
          }
        ],
        headers: [
          {
            sortable: false,
            text: 'STT',
            align: 'center',
            value: 'index'
          },
          {
            sortable: false,
            text: 'M c s',
            align: 'center',
            value: 'maCoSo'
          },
          {
            sortable: false,
            text: 'Tn c s',
            align: 'center',
            value: 'tenCoSo'
          },
          {
            sortable: false,
            text: 'a ch c s',
            align: 'center',
            value: 'diaChiCoSo'
          },
          {
            sortable: false,
            text: 'Tnh/ thnh ph',
            value: 'tinhThanhTen'
          },
          {
            sortable: false,
            text: 'Qun/ huyn',
            align: 'center',
            value: 'quanHuyenTen'
          },
          {
            sortable: false,
            text: 'Phng/ x',
            align: 'center',
            value: 'phuongXaTen'
          },
          {
            sortable: false,
            text: 'Ngi i din',
            align: 'center',
            value: 'nguoiDaiDien'
          },
          {
            sortable: false,
            text: 'S in thoi',
            align: 'center',
            value: 'soDienThoai'
          },
          {
            sortable: false,
            text: 'Thao tc',
            align: 'center',
            value: 'action'
          },
        ],
      }
    },
    created () {
      let vm = this
      vm.$store.commit('SET_INDEXTAB', 3)
      vm.getCoSoYTe()
      vm.getTinhThanh()
    },
    watch: {
      tinhThanh (val) {
        this.thongTinCoSo.TinhThanh_Ma = val
        this.getQuanHuyen(val)
      },
      quanHuyen (val) {
        this.thongTinCoSo.QuanHuyen_Ma = val
        this.getXaPhuong(val)
      },
      xaPhuong (val) {
        this.thongTinCoSo.PhuongXa_Ma = val 
      },
    },
    computed: {
      breakpointName () {
        return this.$store.getters.getBreakpointName
      }
    },
    methods: {
       getCoSoYTe () {
        let vm = this
        let filter = {
          page: 1,
          size: 30
        }
        vm.$store.dispatch('getCoSoYTe', filter).then(function (result) {
          vm.items = result ? result : []
          vm.totalItem = result.length
        })
      },
      getTinhThanh () {
        let vm = this
        let filter = {
        }
        vm.$store.dispatch('getDanhMucTinhThanh', filter).then(function (result) {
          vm.listTinhThanh = result ? result : []
          if (vm.tinhThanh) {
            vm.getQuanHuyen(vm.tinhThanh)
          }
        })
      },
      getQuanHuyen (code) {
        let vm = this
        if (!code) {
          return
        }
        let obj = vm.listTinhThanh.find(function (item) {
          return item.tinhThanhMa == code
        })
        let filter = {
          idParent: obj['id']
        }
        vm.$store.dispatch('getDanhMucQuanHuyen', filter).then(function (result) {
          vm.listQuanHuyen = result ? result : []
          if (vm.quanHuyen) {
            vm.getXaPhuong(vm.quanHuyen)
          }
        })
      },
      getXaPhuong (code) {
        let vm = this
        if (!code) {
          return
        }
        let obj = vm.listQuanHuyen.find(function (item) {
          return item.quanHuyenMa == code
        })
        if (!obj) {
          return
        }
        let filter = {
          idParent: obj['id']
        }
        vm.$store.dispatch('getDanhMucXaPhuong', filter).then(function (result) {
          vm.listXaPhuong = result ? result : []
        })
      },
      addMember (type, user) {
        let vm = this
        vm.typeAction = type
        vm.coSoUpdate = user
        vm.dialogAddMember = true
        if (type === 'add') {
          setTimeout(function () {
            vm.$refs.formAddMember.reset()
            vm.$refs.formAddMember.resetValidation()
          }, 200)
        } else {
          setTimeout(function () {
            vm.thongTinCoSo.MaCoSo = vm.coSoUpdate.maCoSo
            vm.thongTinCoSo.TenCoSo = vm.coSoUpdate.tenCoSo
            vm.thongTinCoSo.DiaChiCoSo = vm.coSoUpdate.diaChiCoSo
            vm.thongTinCoSo.NguoiDaiDien = vm.coSoUpdate.nguoiDaiDien
            vm.tinhThanh = vm.coSoUpdate.tinhThanhMa
            vm.quanHuyen = vm.coSoUpdate.quanHuyenMa
            vm.xaPhuong = vm.coSoUpdate.phuongXaMa
            vm.thongTinCoSo.SoDienThoai = vm.coSoUpdate.soDienThoai

            vm.$refs.formAddMember.resetValidation()
          }, 200)
        }
        
      },
      formatDataInput () {
        let vm = this
        try {
          if (vm.tinhThanh) {
            let obj = vm.listTinhThanh.find(function (item) {
              return item.tinhThanhMa == vm.tinhThanh
            })
            vm.thongTinCoSo.TinhThanh_Ma = vm.tinhThanh
            vm.thongTinCoSo.TinhThanh_Ten = obj ? obj['tinhThanhTen'] : ''
          }
          if (vm.quanHuyen) {
            let obj = vm.listQuanHuyen.find(function (item) {
              return item.quanHuyenMa == vm.quanHuyen
            })
            vm.thongTinCoSo.QuanHuyen_Ma = vm.quanHuyen
            vm.thongTinCoSo.QuanHuyen_Ten = obj ? obj['quanHuyenTen'] : ''
          }
          if (vm.xaPhuong) {
            let obj = vm.listXaPhuong.find(function (item) {
              return item.phuongXaMa == vm.xaPhuong
            })
            vm.thongTinCoSo.PhuongXa_Ma = vm.xaPhuong
            vm.thongTinCoSo.PhuongXa_Ten = obj ? obj['phuongXaTen'] : ''
          }
          console.log('thongTinCoSo', vm.thongTinCoSo)
        } catch (error) {
          vm.processingAction = false
        }
      },
      deleteCoSo (user) {
        let vm = this
        let x = confirm('Bn c chc chn xa c s y t ny?')
        if (x) {
          let filter = {
            id: user['id']
          }
          vm.loading = true
          vm.$store.dispatch('deleteCoSoYTe', filter).then(function () {
            vm.$store.commit('SHOW_SNACKBAR', {
              show: true,
              text: 'Xa thnh cng',
              color: 'success',
            })
            setTimeout(function () {
              vm.getCoSoYTe()
            }, 500)
          }).catch(function () {
            vm.$store.commit('SHOW_SNACKBAR', {
              show: true,
              text: 'Xa tht bi',
              color: 'error',
            })
          })
        }
        
      },
      submitAddMember () {
        let vm = this
        if (vm.$refs.formAddMember.validate()) {
          vm.formatDataInput()
          if (vm.typeAction === 'add') {
            let filter = {
              data: vm.thongTinCoSo
            }
            vm.loading = true
            vm.$store.dispatch('addCoSoYTe', filter).then(userCredential => {
              vm.loading = false
              vm.dialogAddMember = false
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Thm c s thnh cng',
                color: 'success',
              })
              vm.getCoSoYTe()
            })
            .catch((error) => {
              vm.loading = false
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Thm c s y t khng thnh cng',
                color: 'error',
              })
              // ..
            });
          } else {
            let filter = {
              id: vm.coSoUpdate['id'],
              data: vm.thongTinCoSo
            }
            vm.loading = true
            vm.$store.dispatch('updateCoSoYTe', filter).then(function () {
              vm.loading = false
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Cp nht thnh cng',
                color: 'success',
              })
              vm.dialogAddMember = false
              vm.getCoSoYTe()
            }).catch(function () {
              vm.$store.commit('SHOW_SNACKBAR', {
                show: true,
                text: 'Cp nht tht bi',
                color: 'error',
              })
            })
          }
          
        }
      },
      cancelAddMember () {
        let vm = this
        vm.dialogAddMember = false
      },
    },
  }
</script>
<style lang="css" scoped>
  .v-data-table-header-mobile {
    display: none !important;
  }
</style>

